
.info{
    padding: 0 10%;
    padding-bottom: 40px;
}
.info h1{
   text-transform: capitalize; 
}
.info p{
    text-align: justify;
    
}
.head{
    padding: 20px 0;
}
.info-container{
    border-top: 1px solid var(--color2);
}
.info-container h2{
    font-size: 15px;
    color: var(--color2);
    text-transform: capitalize;
    margin: 0;
}
.acc-wrapper{
    margin-top: 10px;
}

.inverter-info{
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 1080px) {
    .inverter-info{
        flex-direction: column;
    }
    p{
        text-align: center !important;
    }
}